export default {
  props: ['story', 'showRecharge', 'list'],
  data () {
    return {
      current: 0,
      money: 0,
      curType: 0,
      styleItems: [
        {
          name: '微信',
          value: 'wx',
          pic: 'icon icon-wx-pay',
        },







      ]
    }
  },
  methods: {
    close: function () {
      this.$emit('closeRechargePop', false)
    },
    moveHandle: function () { // 禁止蒙版下的页面滑动
    },
    changeMoney: function (e) {
      // console.log(e)
      let val = e.detail.value
      this.money = e.detail.value
      for (let i = 0; i < this.list.length; i++) {
        if (val === this.list[i].amount) {
          this.current = i
          break
        } else {
          continue
        }
      }
    },
    typeChange: function (e) {
      this.type = e.detail.value
      let val = e.detail.value
      if (val === 'wx') {
        this.curType = 0
      } else if (val === 'zfb') {
        this.curType = 1
      } else if (val === 'yl') {
        this.curType = 2
      }
    },
    isChecked: function (index) {
      return index === this.current ? true : false
    },
    sure: function () {
      if (this.money === 0) {
        this.money = this.list[0].amount
      }
    }
  }
}
